video {
    object-fit: cover;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: -1;
}

.hero-container {
   background: none;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    /*justify-content: center;*/
    align-items: center;
    margin: 0;
    object-fit: cover;
    font-size: 26px;
}

.hero-container > h1 {
    color: #fff;
    font-size: 42px;
    /*margin-top: -5px;*/
    /*bottom: 800px;*/
}

.hero-container > p {
    margin-top: 8px;
    color: #fff;
    font-size: 42px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

.fa-play-circle {
    /*margin-left: 4px;*/
}

@media screen and (max-width: 960px) {
    .hero-container > h1 {
        font-size: 42px;
        /*margin-top: 10px;*/
    }
}

@media screen and (max-width: 768px) {
    video {
        object-fit: cover;
        width: 100%;
        height: 100%;
        z-index: -1;
    }

    .hero-container > h1 {
        font-size: 21px;
        margin-top: 10px;
    }

    .hero-container {
       width: 100%;
    }

    .btn-mobile {
        display: block;
        text-decoration: none;
    }

    .btn {
        width: 100%;
    }
