@media screen and (max-width: 2560px) {
    .navbar {
        background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(3, 3, 3) 100%);
        height: 80px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 1.2rem;
        position: sticky;
        top: 0;
        z-index: 999;
        width: 100%
        /*margin-right: 0;*/
    }

    .navbar-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 84px;
        width: 100%;
    }

    .navbar-logo {
        color: #fff;
        justify-content: left;
        margin-left: 42px;
        cursor: pointer;
        text-decoration: none;
        font-size: 32px;
        display: flex;
        align-items: center;
        height: 42px;
        /*width: 50px;*/
    }

    .nav-menu {
        list-style: none;
        text-align-last: right;
        display: flex;
        justify-content: right;
        align-items: end;
        margin-right: 42px;
    }

    .nav-item {
        height: 80px;
    }

    .nav-links {
        color: #fff;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        text-decoration: none;
        padding: 0.5rem 0.5rem;
        height: 100%;
        font-size: 32px;
    }

    .nav-links:hover {
        border-bottom: 4px solid #fff;
        transition: all 0.2s ease-out;
    }

    .fa-bars {
        color: #fff;
    }

    .nav-links-mobile {
        display: none;
    }

    .menu-icon {
        display: none;
    }

    .justin {
        padding: 8px 0;
    }

    .hart {
        padding: 8px 0;

    }

    .conger {
        padding: 8px 0;
    }

    .hart-logo {
        display: flex;
        align-items: flex-start;
    }
}
